<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>库存管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">公司商品信息</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>
        {{ type == 1 ? "修改" : "新增" }}公司商品信息</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="companyGoodsInfoAdd">
      <el-form inline ref="params" :model="params" label-width="150px">
        <!-- 存储上边的三级联动 -->
        <el-row>
          <el-form-item label="销售组织" size="mini" :rules="rules.company_id" prop="company_id">
            <getServiceCompany ref="erpCompanyList" v-model="params.company_id" :fatherParams="params" :disabled="type == 2" />
          </el-form-item>
        </el-row>
        <el-row>
          <el-form-item label="商品分类" size="mini" prop="goods_type_id" :rules="{ required: true, message: '请输入选择商品类型' }">
            <jybd-cascader ref="cascader" @cascaderChange="goodsTypeChange" :cascaderVue="cascaderVue"></jybd-cascader>
          </el-form-item>
        </el-row>
        <el-row>
          <el-form-item label="商品品牌" :rules="rules.trademark" prop="brand_name" size="mini">
            <getServiceBrand @focus="serviceBrandFocus" v-model="params.brand_id" ref="getServiceBrand" :fatherParams="params" @change="brandChange" :searchParams="{ goods_type_id: params.goods_type_id }" :type="2" @nameChange="nameChange" />
            <span class="information">如目前搜索结果没有要选择的品牌信息，可在此输入品牌名称，在提交保存时会自动增加对应商品分类的品牌名称</span>
          </el-form-item>
        </el-row>

        <el-row>
          <el-form-item label="商品名称" :rules="rules.goods_name" prop="goods_name" size="mini">
            <el-input v-model="params.goods_name" clearable></el-input>
          </el-form-item>
        </el-row>

        <el-row v-if="modelLabel">
          <el-form-item :label="modelLabel" :rules="{ required: true, message: '请输入' + modelLabel }" prop="goods_model" size="mini">
            <el-input v-model="params.goods_model" clearable @change="getCarModels"></el-input>
            <span class="information">{{ modelLabel }}型号编码，如：H2201、WK20331等</span>
          </el-form-item>
        </el-row>

        <el-row v-if="isHuoHuaSai">
          <el-form-item label="材质" size="mini">
            <el-input v-model="params.goods_texture" clearable @change="getCarModels"></el-input>
            <span class="information">火花塞材质，如：高端陶瓷、高碳陶瓷、金属、陶瓷、陶银等</span>
          </el-form-item>
        </el-row>

        <el-row>
          <el-form-item label="商品单位" :rules="{ required: true, message: '商品单位不能为空' }" prop="unit_id" size="mini">
            <getUnitName v-model="params.unit_id" :fatherParams="params" />
          </el-form-item>
        </el-row>
        <el-row>
          <el-form-item label="销售价" :rules="{ required: true, message: '销售价不能为空' }" prop="guide_price" size="mini">
            <el-input v-model="params.guide_price" @blur="
                $publicFution.toFixedTwo(params, 'guide_price', {
                  type: 'Positive'
                })
              "><template slot="append">元</template></el-input>
            <span class="information">销售价应用于开单指导价及小程序店铺售价，请务必确保准确性</span>
          </el-form-item>
        </el-row>
        <el-row>
          <el-form-item label="自定义编码" size="mini">
            <el-input v-model="params.goods_sn"></el-input>
          </el-form-item>
        </el-row>
        <el-row>
          <el-form-item label="商品图片" size="mini">
            <cupload v-model="params.goods_image" :limit="5" ref="cupload" :allowType="['png', 'jpeg', 'jpg', 'gif']" :extendParams="extendParams" :sizeLimit="2" returnStr="response.data.name_path" url="/service_station/open/Upload/uploadImage">
              <el-button type="primary" style="width: 100%;">选择文件</el-button>
            </cupload>
            <span class="information">
              上传图片的最佳尺寸：800像素*800像素，其他尺寸会影响页效果，格式png，jpeg，jpg，gif。大小不超过2M，商品图片一共可以上传5张.
            </span>
          </el-form-item>
        </el-row>
        <el-row>
          <el-form-item label="车型匹配码" size="mini">
            <el-input v-model="params.oe_code_show" @change="oeCodeShowChange" type="textarea" :rows="5" :cols="20"></el-input>
          </el-form-item>
          <p class="information" style="margin:0px 10px 10px 147px ;width: 50%;">
            注：商品对应的OE码、原厂编码、品牌件码、大厂码、曼牌编码、TRW编码、Ngk火花塞编码等，可以输入多个，多个码请使用逗号、分号或顿号（，；、）分隔；系统会根据填写内容自动匹配适用车型，本信息填写完整性及准确性将影响关联适配车型的准确性。
          </p>
        </el-row>
        <el-row>
          <el-form-item label="适配车型" size="mini">
            <el-button type="primary" style="width: 100%;" @click="isShowSelectModels = true">
              {{
                carId.length || oeCarModles.length ? "修改" : "添加"
              }}适配车型</el-button>
          </el-form-item>
        </el-row>
        <div v-if="isRevealAll">
          <el-row>
            <el-form-item label="工厂编码" size="mini">
              <el-input v-model="params.factory_code" type="text"></el-input>
            </el-form-item>
          </el-row>
          <el-row>
            <el-form-item label="销售税率" size="mini">
              <el-input type="text" placeholder="请输入" maxlength="300" size="mini" @input="$publicFution.limitInput(params, 'taxrate', $event)" v-model="params.taxrate" @change="
                  $publicFution.toFixedTwo(params, 'taxrate', {
                    min: 0,
                    less: 100
                  })
                "><template slot="append">%</template></el-input>
            </el-form-item>
          </el-row>
          <el-row>
            <el-form-item label="停用" size="mini">
              <el-radio v-model="params.is_disabled" :label="1">是</el-radio>
              <el-radio v-model="params.is_disabled" :label="0">否</el-radio>
            </el-form-item>
          </el-row>
          <el-row>
            <el-form-item label="备注" size="mini">
              <el-input v-model="params.remark" type="textarea" :rows="5" :cols="20"></el-input>
            </el-form-item>
          </el-row>
        </div>
        <hr />
        <p style="text-align:center;font-size: 14px;cursor: pointer;color:#409eff" @click="isRevealAll = !isRevealAll">
          <i :class="isRevealAll ? 'el-icon-caret-top' : 'el-icon-caret-bottom'"></i>
          <span>{{ isRevealAll ? "收起" : "展开全部" }}</span>
        </p>
      </el-form>
    </div>
    <div class="dialog-footer">
      <el-button @click="goBack">取 消</el-button>
      <el-button type="primary" @click="submit(1)">保 存</el-button>
      <el-button type="primary" @click="submit(2)">保存并新增</el-button>
    </div>
    <selectModels v-model="isShowSelectModels" :oeCarModles="oeCarModles" :carModles="carId" @change="carModelsChange" />
  </div>
</template>

<script>
import { paramsModule } from "./config/companyGoodsInfoAdd";
import selectModels from "common/components/plugin/service/selectModels.vue";
export default {
  name: "service_station_inventory_goods_add",
  data() {
    return {
      type: 0, // type 为1是修改
      cascaderVue: [], // 分类回显
      modelLabel: "商品型号",
      isRevealAll: false,
      isHuoHuaSai: false,
      huoHuaSaiList: [],
      params: this.$publicFution.deepCopy(paramsModule),
      extendParams: {
        directory: "service_station",
      },
      parameter_list: {},
      isShowSelectModels: false,
      carId: [],
      oeCarModles: [],
    };
  },
  components: {
    selectModels,
  },
  methods: {
    brandChange() {
      this.params.add_brand_name = "";
      this.getCarModels();
    },
    nameChange(val) {
      this.params.add_brand_name = val;
      this.params.brand_name = val;
      this.params.brand_id = "";
    },
    serviceBrandFocus() {
      if (!this.params.goods_type_id) {
        this.$message.warning("请先选择商品分类");
      }
    },
    oeCodeShowChange(val) {
      var result = "";
      if (val) {
        val = val.split(/,|，|;|、|；/g).filter((val) => val);
        result = val.join(",");
      }
      this.params.oe_code = result;
    },
    carModelsChange(arr, id) {
      this.oeCarModles = [];
      this.car_lib_id = arr;
      this.carId = id;
    },

    getCarModels() {
      if (this.params.brand_name && this.params.goods_type_id) {
        for (var i in this.parameter_list) {
          if (i !== "材质") {
            this.parameter_list[i].attribute_value_name =
              this.params.goods_model;
          } else {
            this.parameter_list[i].attribute_value_name =
              this.params.goods_texture;
          }
        }
        this.$api
          .serviceStationGetOeCodeOrCarLibraryList({
            parameter_list: JSON.stringify(this.parameter_list),
            brand_name: this.params.brand_name,
            goods_type_id: this.params.goods_type_id,
            goods_type_name: this.params.goods_type_name,
          })
          .then(({ data }) => {
            if (data.code == 200) {
              var box = [];
              for (var i in data.data.car_library) {
                box = box.concat(data.data.car_library[i]);
              }
              var oe = [];
              for (var i in data.data.oe_code) {
                oe = oe.concat(data.data.oe_code[i]);
              }
              this.params.oe_code = this.params.oe_code_show = oe.join(",");
              this.renderCarModels(box);
            }
          });
      }
    },
    renderAttribute(val, callBack) {
      if (val) {
        if (val[2] == 1942) {
          this.isHuoHuaSai = true;
          this.$api
            .serviceStationGetShopGoodsAttributeValueList({
              goods_type_id: val[2],
              attribute_name: "材质",
            })
            .then(({ data }) => {
              if (data.code == 200) {
                this.huoHuaSaiList = data.data[0].attr_value_list;
              }
            });
        } else {
          this.isHuoHuaSai = false;
        }
        this.cascaderVue = val;
        this.params.goods_type_id = val[2];
        this.$api
          .serviceStationGetTypeParameterList({ goods_type_id: val[2] })
          .then(({ data }) => {
            if (data.code == 200) {
              this.modelLabel = Object.keys(data.data).filter(
                (val) => val != "材质"
              )[0];
              this.parameter_list = data.data;
              callBack && callBack();
            }
          });
      } else {
        this.isHuoHuaSai = false;
      }
    },
    goodsTypeChange(val, obj) {
      this.renderAttribute(val, () => {
        this.getCarModels();
      });
      this.params.goods_type_name = obj[0].label;
      this.$refs.params.validateField("goods_type_id");
      this.params.add_brand_name = "";
      this.params.brand_name = "";
      this.params.brand_id = "";
      this.$refs.getServiceBrand.clear();
    },
    submit(step) {
      this.$refs.params.validate((valid) => {
        if (valid) {
          var params = this.$publicFution.deepCopy(this.params);
          var url = "serviceStationAddGoods";
          if (this.type) {
            url = "serviceStationEditGoods";
          }
          params.car_library = [];
          params.car_library_name = [];
          this.car_lib_id.forEach((val) => {
            if (val.match_type != 1) {
              params.car_library.push(val.car_lib_id);
              params.car_library_name.push(val.sales_name);
            }
          });
          params.car_library.join(",");
          params.car_library_name.join(",");
          delete params.parameter_list;
          this.$api[url](params).then(({ data }) => {
            if (data.code == 200) {
              this.$message.success(data.msg);
              if (step === 1) {
                this.goBack();
              }
            }
          });
        }
      });
    },
    renderCarModels(arr) {
      if (arr) {
        this.carId = [];
        this.car_lib_id = [];
        this.oeCarModles = [];
        arr.forEach((val) => {
          if (val.match_type == 1) {
            this.oeCarModles.push(val.car_lib_id);
          } else {
            this.carId.push(val.car_lib_id);
          }
        });
        this.car_lib_id = arr;
      }
    },
    goBack() {
      this.$router.push({
        path: "/service_station_goods/service_station_inventory_goods",
      });
    },
  },
  mounted() {
    if (this.$route.query.id) {
      this.carId = [];
      this.car_lib_id = [];
      this.oeCarModles = [];
      this.type = 1;
      this.$api
        .serviceStationgetGoodsInfo({
          inventory_goods_id: this.$route.query.id,
        })
        .then(({ data }) => {
          if (data.code == 200) {
            data.data.oe_code_show = data.data.oe_code;
            this.params = data.data;
            this.cascaderVue = [
              data.data.goods_type_id_1 - 0,
              data.data.goods_type_id_2 - 0,
              data.data.goods_type_id - 0,
            ];
            this.renderAttribute(this.cascaderVue);
            this.$refs.cupload._initList(this.params.image);
            this.$refs.getServiceBrand.query = data.data.brand_name;
            this.renderCarModels(data.data.car_library);
          }
        });
    } else {
      setTimeout(() => {
        this.$refs.erpCompanyList._setList({
          company_id: Number(localStorage.company_id),
          company_name: localStorage.company_name,
        });
      }, 500);
      Object.assign(this.params, {
        company_id: Number(localStorage.company_id),
      });
    }
  },
};
</script>

<style scoped lang="less">
.companyGoodsInfoAdd {
  /deep/ .el-form-item--mini.el-form-item {
    width: 100%;
    .el-textarea {
      width: 500px;
    }
  }
  .information {
    color: rgba(0, 0, 0, 0.5);
    font-size: 12px;
  }
}
</style>
