<template>
  <div class="params">
    <vxe-modal v-model="value" title='适配车型' @hide="close" width="1200" :esc-closable='true' destroy-on-close height="800" min-width="600" min-height="100" resize remember storage transfer>
      <div v-loading='loading'>
        <selectModelAdd v-show='type == 1' @close='addClose' @submitData="renderAll" :carId='carId' ref='selectModelAdd' />
        <div v-show='type !== 1'>
          <div>
            <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
              <el-form-item :label="val" size="mini" v-for='(val,key) in paramsModule' :key='key'>
                <el-select clearable v-model="params[key]" :placeholder="'请选择' + val" @focus="getSelectOptions(key)">
                  <el-option :label="twoKey" :value="twoKey" v-for="(val,twoKey) in selectOptionsCopy[key]" :key='twoKey'></el-option>
                </el-select>
              </el-form-item>
              <el-form-item size="mini" class="search-style search">
                <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
                <el-button plain @click="onReset" size="mini">重置</el-button>
              </el-form-item>
            </el-form>
          </div>
          <div class="add_delet">
            <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addCarModels" class="addBtn" size="mini">添加其他车型</el-button>
            <el-button plain icon="el-icon-delete" @click="delCarModels" size="mini">删除车型</el-button>
          </div>
          <div>
            <xTable ref='xTable' :tableColumn='tableColumn' :xData='renderData' @selectChange='(row) => selection = row.selection' />
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 10, 30]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="toTal">
            </el-pagination>
          </div>
          <div class="dialog-footer">
            <el-button @click="close">取 消</el-button>
            <el-button type="primary" @click="submit">确定</el-button>
          </div>
        </div>

      </div>
    </vxe-modal>
  </div>
</template>

<script>
import selectModelAdd from "./selectModelAdd.vue";
import get_car_librarys_csv from "common/components/plugin/service/config/carModel.js";
import {
  mixins,
  paramsModule,
  selectOptionsModule,
} from "./config/selectModels";
export default {
  mixins: [mixins],
  data() {
    return {
      loading: false,
      type: 2, // 1为新增 2为修改
      params: this.$publicFution.deepCopy(paramsModule),
      selectOptions: this.$publicFution.deepCopy(selectOptionsModule),
      selectOptionsCopy: this.$publicFution.deepCopy(selectOptionsModule),
      renderData: [],
      selection: [],
      carId: [],
      currentPage: 1,
      pageSize: 10,
      toTal: 0,
    };
  },
  created() {
    this.init();
  },
  watch: {
    value(newVal) {
      if (newVal === true) {
        if (this.carModles.length || this.oeCarModles.length) {
          setTimeout(() => {
            this.renderAll(this.carModles, this.oeCarModles);
          }, 50);
        } else {
          this.addCarModels();
        }
      }
    },
  },
  props: {
    value: null,
    data: {
      type: Object,
      default: () => {},
    },
    goods: {
      type: Object,
      default: () => {},
    },
    carModles: {
      type: Array,
      default: () => [],
    },
    oeCarModles: {
      type: Array,
      default: () => [],
    },
  },
  components: {
    selectModelAdd,
  },
  methods: {
    init() {
      this.AllTableData = [];
      this.tableDate = [];
      this.allData = {};
      get_car_librarys_csv((res) => {
        this.allData = res.data;
        setTimeout(() => {
          this.$refs.selectModelAdd.init(res.data);
        });
      });
    },
    addClose() {
      if (this.carModles.length) {
        this.type = 2;
      } else {
        this.close();
      }
    },
    // 获取下拉框数据
    getSelectOptions(key) {
      this.selectOptionsCopy[key] = this.selectOptions[key];
    },
    onSearch(num) {
      this.currentPage = num ? num : this.currentPage;
      var result = [];
      var selectOptions = this.$publicFution.deepCopy(selectOptionsModule);
      this.AllTableData.forEach((val) => {
        var step = true;
        for (var i in this.selectOptions) {
          if (this.params[i] && this.params[i] !== val[i]) {
            step = false;
            break;
          }
        }
        if (step) {
          for (var j in this.selectOptions) {
            if (!selectOptions[j][val[j]] && val[j] && val[j].trim()) {
              selectOptions[j][val[j]] = "断剑重铸之日骑士归来之时";
            }
          }
          result.push(val);
        }
      });
      this.selectOptions = selectOptions;
      this.tableDate = result;
      this.toTal = this.tableDate.length;
      this.renderTable();
    },
    onReset() {
      this.tableDate = this.AllTableData.concat([]);
      this.params = this.$publicFution.deepCopy(paramsModule);
      this.onSearch(1);
    },
    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.renderTable();
    },
    handleCurrentChange(index) {
      this.currentPage = index;
      this.renderTable();
    },
    // 删除车型
    delCarModels() {
      if (this.selection.length) {
        var ids = [];
        this.AllTableData = this.AllTableData.filter((val) => {
          if (!this.selection.includes(val)) {
            ids.push(val.car_lib_id);
            return true;
          }
        });
        this.carId = ids;
        this.onSearch();
      } else {
        this.$message.warning("请选择车型");
      }
    },
    // 添加车型
    addCarModels() {
      this.type = 1;
      this.$refs.selectModelAdd.initRender(this.AllTableData[0]);
    },
    // 渲染表格数据
    renderTable() {
      this.renderData = this.tableDate.slice(
        (this.currentPage - 1) * this.pageSize,
        this.currentPage * this.pageSize
      );
    },
    // 获取匹配数据
    getMatchingData(selectOptions, ids, type) {
      var result = [];
      for (var i in this.allData) {
        this.allData[i].forEach((val) => {
          if (ids.includes(val.car_lib_id)) {
            val.match_type = val.match_type == 1 ? val.match_type : type;
            result.push(val);
            for (var j in selectOptions) {
              if (!selectOptions[j][val[j]] && val[j] && val[j].trim()) {
                selectOptions[j][val[j]] = "哈撒给";
              }
            }
          }
        });
      }
      return result;
    },
    // 根据id得到数组,并且回显查询项
    renderAll(ids, oeCarModles) {
      this.type = 2;
      this.loading = true;
      setTimeout(() => {
        var selectOptions = this.$publicFution.deepCopy(selectOptionsModule);
        var result = this.getMatchingData(selectOptions, ids, 0);
        if (oeCarModles) {
          result = result.concat(
            this.getMatchingData(selectOptions, oeCarModles, 1)
          );
          this.carId = ids.concat(this.oeCarModles);
        } else {
          this.carId = ids;
        }
        this.selectOptions = selectOptions;
        this.AllTableData = result;
        this.tableDate = result.concat([]);
        this.toTal = result.length;
        this.currentPage = 1;
        this.renderTable();
        this.loading = false;
        if (this.carModles.length == 0 && this.oeCarModles.length == 0) {
          this.submit();
        }
      }, 50);
    },
    close() {
      this.$emit("input", false);
    },
    submit() {
      this.$emit("change", this.AllTableData, this.carId);
      this.close();
    },
  },
};
</script>

