// 获取全部品牌
export function getAllBrandBox(list) {
  var tempObj = {};
  // 键名(字母索引)排序
  const sortKeys = Object.keys(list).sort()
  // 将每个字母索引存入临时对象
  for (var k = 0; k < sortKeys.length; k++) {
    let key = sortKeys[k]
    if (!tempObj[key]) {
      tempObj[key] = {}
    }
    // 将品牌所对应的车系，存入对应字母索引下的品牌之中
    for (var i = 0; i < list[key].length; i++) {
      if (!tempObj[key][list[key][i].brand_name]) {
        tempObj[key][list[key][i].brand_name] = [];
      }
      tempObj[key][list[key][i].brand_name].push(list[key][i]);
    }
  }
  return tempObj
}
// 通过关键字查询品牌
export function searchQuery({ list, letter, keywords }) {
  if (letter) {
    // 已指定字母索引，则在当前索引下筛选符合条件品牌
    const tempArr = list[letter] || {}
    const tempObj = {}
    Object.keys(tempArr).forEach(item => {
      if (item.includes(keywords)) {
        tempObj[item] = tempArr[item]
      }
    })
    return tempObj
  } else {
    // 未指定字母索引，则在所有品牌下筛选符合条件品牌
    const tempObj = list
    const resultObj = {} // 结果对象
    for (const letter in tempObj) {
      let brandNames = Object.keys(tempObj[letter])
      // 筛选品牌名称中包含关键字的品牌
      let includesBrands = brandNames.filter(item => item.includes(keywords))
      if (includesBrands.length) {
        resultObj[letter] = {}
        // 将符合条件的品牌下的所有车系存入结果对象中
        for (let index = 0; index < includesBrands.length; index++) {
          let brandName = includesBrands[index]
          resultObj[letter][brandName] = tempObj[letter][brandName]
        }
      }
    }
    return resultObj
  }
}
var car = ["三厢", "三厢车", "两厢车", "两厢", "掀背车", "旅行车"]
var newPotentialSource = ['EV', '氢燃料', '增程式']
var sportsCar = ["硬顶敞篷车", "软顶敞篷车", "硬顶跑车", "跑车"]
function getCarType({ car_type, displacement }) {
  if (newPotentialSource.includes(displacement)) {
    return '新能源'
  }
  if (car_type.toUpperCase() === 'MPV') {
    return 'MPV'
  }

  if (car_type.toUpperCase() === 'SUV') {
    return 'SUV'
  }
  if (car_type === '客车') {
    return '客车'
  }
  if (sportsCar.includes(car_type)) {
    return '跑车'
  }
  if (car.includes(car_type)) {
    return '轿车'
  }
  return '其他'
}

export function getCarSeriesBox(arr) {
  var obj = {};
  var total = 0
  var carSeriesArr = [] // 车系
  arr.forEach((val) => {
    if (val.source != 1) {
      if (!obj[val.show_name]) {
        obj[val.show_name] = {}
      }
      var carType = getCarType(val)
      if (!obj[val.show_name][carType]) {
        obj[val.show_name][carType] = {}
      }
      if (!obj[val.show_name][carType][val.car_series]) {
        obj[val.show_name][carType][val.car_series] = [];
      }
      if (!carSeriesArr.includes(val.car_series)) {
        carSeriesArr.push(val.car_series)
      }
      obj[val.show_name][carType][val.car_series].push(val);
      total++
    }
  });
  return {
    obj,
    total,
    carSeriesArr,
  }
}
// 获取全部品牌(公司商品信息)
export function getAllBrand(list) {
  var brandObj = {};
  var brandId = {}
  var total = 0
  // 键名(字母索引)排序
  // 将每个字母索引存入临时对象
  for (var key in list) {
    if (!brandObj[key]) {
      brandObj[key] = {}
    }
    // 将品牌所对应的车系，存入对应字母索引下的品牌之中
    for (var i = 0; i < list[key].length; i++) {
      var obj = list[key][i]
      if (!brandObj[key][obj.brand_name]) {
        brandObj[key][obj.brand_name] = [];
      }
      if (!brandId[obj.brand_name]) {
        brandId[obj.brand_name] = []
      }
      total++
      brandObj[key][obj.brand_name].push(obj);
      brandId[obj.brand_name].push(obj.car_lib_id)
    }
  }
  return {
    brandObj,
    brandId,
    total
  }
}
// 获取车型对象
export function getCarSeries(brandBox, id) {
  var obj = {}
  var echoObj = null
  if (id) { // 优化所以复制一份
    brandBox.forEach(val => {
      if (val.car_lib_id == id) {
        echoObj = val
      }
      if (!obj[val.car_series]) {
        obj[val.car_series] = {}
      }
      if (!obj[val.car_series][val.displacement]) {
        obj[val.car_series][val.displacement] = {}
      }
      if (!obj[val.car_series][val.displacement][val.years]) {
        obj[val.car_series][val.displacement][val.years] = []
      }
      obj[val.car_series][val.displacement][val.years].push(val)
    })
  } else {
    brandBox.forEach(val => {
      if (!obj[val.car_series]) {
        obj[val.car_series] = {}
      }
      if (!obj[val.car_series][val.displacement]) {
        obj[val.car_series][val.displacement] = {}
      }
      if (!obj[val.car_series][val.displacement][val.years]) {
        obj[val.car_series][val.displacement][val.years] = []
      }
      obj[val.car_series][val.displacement][val.years].push(val)
    })
  }
  return { obj, echoObj }
}