 * @info: 选择车型
-->
<template>
  <div class="params" v-loading='loading'>
    <div class="matchingModel">
      <div class='matchingModelSearch'>
        <p class='matchingModelAll'>
          <el-button plain size='mini'>
            <el-checkbox v-model="isAll" @change="isAllChange">通用全部车型</el-checkbox>
          </el-button>
          <span>已选择车型 {{ selectedCarNumber }}</span>
        </p>

        <el-tag v-for="item in letterBox" :key="item" :type="item" @click="changeLetter(item)" :effect="current.letter === item ? 'dark' : 'plain'">
          {{ item }}
        </el-tag>

        <p class='matchingModelBrand'>
          <el-button type="text" @click="setBrand(key)" :class="current.brand == key ? 'active': ''" size='mini' v-for='(val,key) in brandBox' :key='key'>{{ key }}
          </el-button>
        </p>
      </div>
      <div class='matchingModelResultBox' v-if='brandBox'>
        <el-row>
          <el-col :span='4'>
            <h4>品牌</h4>
            <div>
              <el-checkbox v-model="selectModel.brand" :label="current.brand" class='carModelActive' @change="brandChange"></el-checkbox>
            </div>
          </el-col>
          <el-col :span='6'>
            <h4>车系</h4>
            <div>
              <el-checkbox-group v-model="selectModel.series">
                <el-checkbox v-for="key in carSeriesKeys" :key='key' :value='key' :class="current.series === key ? 'carModelActive' :'' " :label="key" @click.native="seriesChange(key);setCarSeries(key, 2)">
                  <span @click.stop.prevent="setCarSeries(key)">{{ key }}</span>
                </el-checkbox>
              </el-checkbox-group>
            </div>
          </el-col>
          <el-col :span='2'>
            <h4>排量</h4>
            <div>
              <el-checkbox-group v-model="selectModel.displacement">
                <el-checkbox v-for="(val,key) in carSeries[current.series]" :key='key' :class="current.displacement === key ? 'carModelActive' :'' " :value='key' :label="key" @click.native="displacementChange(key);setDisplacement(key,2)">
                  <span @click.stop.prevent="setDisplacement(key)">{{ key }}</span>
                </el-checkbox>
              </el-checkbox-group>
            </div>
          </el-col>
          <el-col :span='2'>
            <h4>年款</h4>
            <div v-if='carSeries[current.series]'>
              <el-checkbox-group v-model="selectModel.year">
                <el-checkbox v-for="(val,key) in carSeries[current.series][current.displacement]" :class="current.year === key ? 'carModelActive' :'' " :key='key' :value='key' :label="key" @click.native="yearChange(key); setYear(key,2)">
                  <span @click.stop.prevent="setYear(key)">{{ key }}</span>
                </el-checkbox>
              </el-checkbox-group>
            </div>
          </el-col>
          <el-col :span='10'>
            <h4>配置</h4>
            <div v-if='carSeries[current.series] && carSeries[current.series][current.displacement]'>
              <el-checkbox-group v-model="selectModel.models">
                <el-checkbox v-for="(val,key) in carSeries[current.series][current.displacement][current.year]" :key='key' :label="val.car_lib_id" @click.native="modelsChange(val.car_lib_id)">{{ val.sales_name }}</el-checkbox>
              </el-checkbox-group>
            </div>
          </el-col>
        </el-row>

      </div>
      <div class="dialog-footer">
        <el-button @click="close">取 消</el-button>
        <el-button type="primary" @click="submit">确定</el-button>
      </div>

    </div>
  </div>
</template>

<script>
import {
  getAllBrand,
  getCarSeries,
} from "common/components/plugin/service/config/selectModel.js";
export default {
  data() {
    var letterBox = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
    return {
      loading: false,
      letterBox: letterBox.split(""),
      selectModelsId: {}, // 选中车型的id
      carSeriesKeys: [],
      isAll: false, // 是否全部车型
      selectModel: {
        brand: false,
        series: [],
        displacement: [],
        year: [],
        models: [],
      }, // 选中的对象
      current: {
        letter: "A",
        brand: "",
        series: "",
        displacement: "",
        year: "",
      },
      unRenderArray: [], // 优化未渲染id数组
      renderAllObj: null, //全选逻辑
      checkList: "",
      selectedCarNumber: 0,
      brandBox: {}, // 品牌
      carSeries: {}, // 车系
    };
  },
  props: {
    value: null,
    carId: {
      type: Array,
      default: () => [],
    },
    isGeneralModels: {
      // 是否是通用车型
      type: Boolean,
      default: false,
    },
  },
  created() {
    this.allData = {};
    this.allId = {};
    this.allTotal = 0;
    this.isDisabled = false;
  },
  methods: {
    // 准备工作
    init(data, type) {
      var obj = getAllBrand(data);
      this.allData = obj.brandObj; // 全部数据
      this.allId = obj.brandId;
      this.allTotal = obj.total;
      this.initSelectModelsId();
      if (type) {
        this.getBrand();
      }
    },
    // 初始化
    initRender(row = null) {
      this.loading = true;
      this.unRenderArray = [];
      setTimeout(() => {
        if (this.isGeneralModels) {
          this.selectModelsId = this.$publicFution.deepCopy(this.allId);
        } else if (this.carId.length) {
          this.initSelectModelsId("德玛西亚");
          this.unRenderArray = this.carId.concat([]);
          this.initSingleSelectModelsId(row.brand_letter, row.brand_name);
        }
        this.getBrand(row);
        this.loading = false;
      }, 50);
    },
    // 渲染品牌选择id单个
    initSingleSelectModelsId(letter, brand_name) {
      var result = [];
      // this.allData[letter][brand_name].map((val) => {
      //   var index = this.unRenderArray.indexOf(val.car_lib_id);
      //   if (index !== -1) {
      //     result.push(val.car_lib_id);
      //     this.unRenderArray.splice(index, 1);
      //   }
      // });
      this.unRenderArray = this.unRenderArray.filter((val) => {
        if (
          this.allData[letter][brand_name].some(
            (twoVal) => twoVal.car_lib_id === val
          )
        ) {
          result.push(val);
          return false;
        }
        return true;
      });
      this.selectModelsId[brand_name] = result;
    },
    // 初始化品牌选择id
    initSelectModelsId(item) {
      for (var i in this.allData) {
        Object.keys(this.allData[i]).forEach((val) => {
          this.selectModelsId[val] = item ? item : [];
        });
      }
    },
    getBrand(obj) {
      this.current.letter = obj ? obj.brand_letter : this.current.letter;
      this.brandBox = Object.freeze(this.allData[this.current.letter]);
      if (this.brandBox) {
        this.setBrand(obj ? obj.brand_name : Object.keys(this.brandBox)[0]);
      }
    },
    // 是否是通用车型
    isAllChange(val) {
      this.unRenderArray = [];
      if (val) {
        this.selectModelsId = this.$publicFution.deepCopy(this.allId);
      } else {
        this.initSelectModelsId();
      }
      this.renderCheckbox();
    },
    // 品牌改变
    brandChange(val) {
      if (val) {
        var box = [];
        for (var i in this.carSeries) {
          for (var j in this.carSeries[i]) {
            for (var k in this.carSeries[i][j]) {
              this.carSeries[i][j][k].forEach((val) =>
                box.push(val.car_lib_id)
              );
            }
          }
        }
        this.selectModelsId[this.current.brand] = box;
      } else {
        this.selectModelsId[this.current.brand] = [];
      }
      this.renderCheckbox();
    },
    // 车型改变
    seriesChange(key) {
      if (!this.isDisabled) {
        this.isDisabled = true;
        setTimeout(() => {
          this.isDisabled = false;
          if (this.selectModel.series.includes(key)) {
            for (var i in this.carSeries[key]) {
              for (var j in this.carSeries[key][i]) {
                for (var k in this.carSeries[key][i][j]) {
                  this.arrPush(
                    this.selectModelsId[this.current.brand],
                    this.carSeries[key][i][j][k].car_lib_id
                  );
                }
              }
            }
          } else {
            for (var i in this.carSeries[key]) {
              for (var j in this.carSeries[key][i]) {
                for (var k in this.carSeries[key][i][j]) {
                  this.arrRemove(
                    this.selectModelsId[this.current.brand],
                    this.carSeries[key][i][j][k].car_lib_id
                  );
                }
              }
            }
          }
          this.renderCheckbox();
        }, 50);
      }
    },
    // 排量改变
    displacementChange(key) {
      if (!this.isDisabled) {
        this.isDisabled = true;
        var displacement = this.carSeries[this.current.series];
        setTimeout(() => {
          this.isDisabled = false;
          if (this.selectModel.displacement.includes(key)) {
            for (var i in displacement[key]) {
              displacement[key][i].forEach((val) => {
                this.arrPush(
                  this.selectModelsId[this.current.brand],
                  val.car_lib_id
                );
              });
            }
          } else {
            for (var i in displacement[key]) {
              displacement[key][i].forEach((val) => {
                this.arrRemove(
                  this.selectModelsId[this.current.brand],
                  val.car_lib_id
                );
              });
            }
          }
          this.renderCheckbox();
        }, 50);
      }
    },
    // 年款变化
    yearChange(key) {
      if (!this.isDisabled) {
        this.isDisabled = true;
        var year =
          this.carSeries[this.current.series][this.current.displacement];
        setTimeout(() => {
          this.isDisabled = false;
          if (this.selectModel.year.includes(key)) {
            year[key].forEach((val) => {
              this.arrPush(
                this.selectModelsId[this.current.brand],
                val.car_lib_id
              );
            });
          } else {
            year[key].forEach((val) => {
              this.arrRemove(
                this.selectModelsId[this.current.brand],
                val.car_lib_id
              );
            });
          }
          this.renderCheckbox();
        }, 50);
      }
    },
    // 配置变化
    modelsChange(id) {
      if (!this.isDisabled) {
        this.isDisabled = true;
        setTimeout(() => {
          this.isDisabled = false;
          if (this.selectModel.models.includes(id)) {
            this.arrPush(this.selectModelsId[this.current.brand], id);
          } else {
            this.arrRemove(this.selectModelsId[this.current.brand], id);
          }
          this.renderCheckbox();
        }, 50);
      }
    },
    // 从配置开始回显  从下往上
    renderCheckbox() {
      // 配置
      var models =
        this.carSeries[this.current.series][this.current.displacement][
          this.current.year
        ];
      var selectId = this.selectModelsId[this.current.brand];
      var length = models.length;
      models.forEach((val) => {
        if (selectId.includes(val.car_lib_id)) {
          this.arrPush(this.selectModel.models, val.car_lib_id);
        } else {
          this.arrRemove(this.selectModel.models, val.car_lib_id);
        }
      });
      // 年款
      var isSelectAll = true;
      if (length === this.selectModel.models.length) {
        this.arrPush(this.selectModel.year, this.current.year);
      } else {
        this.arrRemove(this.selectModel.year, this.current.year);
        isSelectAll = false;
      }
      var year = this.carSeries[this.current.series][this.current.displacement];
      for (var i in year) {
        if (i !== this.current.year) {
          if (year[i].every((val) => selectId.includes(val.car_lib_id))) {
            this.arrPush(this.selectModel.year, i);
          } else {
            isSelectAll = false;
            this.arrRemove(this.selectModel.year, i);
          }
        }
      }
      // 排量
      if (isSelectAll) {
        this.arrPush(this.selectModel.displacement, this.current.displacement);
      } else {
        this.arrRemove(
          this.selectModel.displacement,
          this.current.displacement
        );
      }
      var displacement = this.carSeries[this.current.series];
      for (var i in displacement) {
        if (i !== this.current.displacement) {
          var displacementIsSelectAll = true;
          for (var j in displacement[i]) {
            if (
              !displacement[i][j].every((val) =>
                selectId.includes(val.car_lib_id)
              )
            ) {
              displacementIsSelectAll = false;
              break;
            }
          }
          if (displacementIsSelectAll) {
            this.arrPush(this.selectModel.displacement, i);
          } else {
            this.arrRemove(this.selectModel.displacement, i);
            isSelectAll = false;
          }
        }
      }
      // 车系
      if (isSelectAll) {
        this.arrPush(this.selectModel.series, this.current.series);
      } else {
        this.arrRemove(this.selectModel.series, this.current.series);
      }
      var series = this.carSeries;
      for (var i in series) {
        if (i !== this.current.series) {
          var seriesIsSelectAll = true;
          for (var j in series[i]) {
            for (var m in series[i][j]) {
              if (
                !series[i][j][m].every((val) =>
                  selectId.includes(val.car_lib_id)
                )
              ) {
                seriesIsSelectAll = false;
                isSelectAll = false;
                break;
              }
            }
            if (!seriesIsSelectAll) break;
          }
          if (seriesIsSelectAll) {
            this.arrPush(this.selectModel.series, i);
          } else {
            this.arrRemove(this.selectModel.series, i);
          }
        }
      }
      // 品牌
      if (isSelectAll) {
        this.selectModel.brand = true;
      } else {
        this.selectModel.brand = false;
      }
      // 计算总条数
      var total = this.unRenderArray.length;
      for (var z in this.selectModelsId) {
        total += Array.isArray(this.selectModelsId[z])
          ? this.selectModelsId[z].length
          : 0;
      }
      this.selectedCarNumber = total;
      if (total < this.allTotal) {
        this.isAll = false;
      } else {
        this.isAll = true;
      }
      this.loading = false;
    },
    // 数组添加某个元素
    arrPush(arr, val) {
      var num = arr.indexOf(val);
      if (num === -1) {
        arr.push(val);
      }
    },
    // 数组删除某个原素
    arrRemove(arr, val) {
      var num = arr.indexOf(val);
      if (num != -1) {
        arr.splice(num, 1);
      }
    },
    // 设置品牌
    setBrand(val) {
      this.loading = true;
      setTimeout(() => {
        this.selectModel.series = [];
        this.current.brand = val;

        if (this.selectModelsId[this.current.brand] === "德玛西亚") {
          this.initSingleSelectModelsId(
            this.current.letter,
            this.current.brand
          );
        }

        var { obj, echoObj } = Object.freeze(
          getCarSeries(
            this.brandBox[val],
            this.selectModelsId[this.current.brand][0]
          )
        );
        this.echoObj = echoObj;
        this.carSeriesKeys = Object.keys(obj).sort();
        this.carSeries = obj;
        this.setCarSeries(
          this.echoObj
            ? this.echoObj.car_series
            : this.carSeriesKeys[0]
        );
      }, 50);
    },
    // 设置车系
    setCarSeries(val, step) {
      this.selectModel.displacement = [];
      this.current.series = val;
      this.setDisplacement(
        this.echoObj
          ? this.echoObj.displacement
          : Object.keys(this.carSeries[val])[0],step
      );
    },
    // 设置排量
    setDisplacement(val,step) {
      this.selectModel.year = [];
      this.current.displacement = val;
      this.setYear(
        this.echoObj
          ? this.echoObj.years
          : Object.keys(this.carSeries[this.current.series][val])[0],step
      );
    },
    // 设置年款
    setYear(val, step) {
      this.selectModel.models = [];
      this.current.year = val;
      if (step !== 2) {
        this.renderCheckbox();
      }
      this.echoObj = null;
    },
    changeLetter(letter) {
      this.current.letter = letter;
      this.getBrand();
    },
    close() {
      this.$emit("close");
    },
    submit() {
      var result = this.unRenderArray;
      for (var i in this.selectModelsId) {
        result = result.concat(
          Array.isArray(this.selectModelsId[i]) ? this.selectModelsId[i] : []
        );
      }
      this.$emit("submitData", result);
    },
  },
};
</script>

<style  lang='less'>
.matchingModel {
  .matchingModelSearch {
    text-align: center;
    .el-tag {
      cursor: pointer;
    }
    .matchingModelAll {
      margin-bottom: 10px;
    }
    .matchingModelBrand {
      text-align: left;
      .el-button--mini {
        padding: 0px;
        cursor: pointer;
        color: rgba(0, 0, 0, 0.5);
      }
      .active {
        color: #409eff;
      }
    }
  }
  .matchingModelResultBox {
    h4 {
      text-align: center;
      margin: 10px;
    }
    .el-checkbox {
      display: block;
      width: 100%;
    }
    .el-row {
      height: 520px;
      overflow-y: scroll;
    }
  }
  .carModelActive {
    .el-checkbox__label {
      color: red !important;
    }
  }
}
</style>